import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  NgbCalendar,
  NgbCalendarGregorian,
  NgbCalendarHebrew, NgbDate,
  NgbDatepickerI18n,
  NgbDatepickerI18nHebrew,
  NgbDateStruct
} from '@ng-bootstrap/ng-bootstrap';
import { FormControl } from '@angular/forms';
import { TranslateService, TranslationChangeEvent } from '@ngx-translate/core';
import * as moment from 'moment';

@Component({
  selector: 'sim-heb-datepicker',
  templateUrl: './heb-datepicker.component.html',
  styleUrls: ['./heb-datepicker.component.css'],
  providers: [
    // {provide: NgbCalendar, useClass: NgbCalendarGregorian},
    // {provide: NgbDatepickerI18n, useClass: NgbDatepickerI18nHebrew}
  ]
})
export class HebDatepickerComponent implements OnInit {

  @Input() label: string;
  @Input() control: FormControl = new FormControl();
  @Input() minDate: Date;
  @Input() maxDate: Date;

  // @Output() onChange: EventEmitter<any> = new EventEmitter();

  currentLang: string;

  struct: NgbDateStruct;

  constructor(private calendar: NgbCalendar, public i18n: NgbDatepickerI18n, private translateService: TranslateService) { }

  ngOnInit(): void {
    // const [year, month, day] = moment(this.control.value).format("YYYY-MM-DD").split('-');
    // this.struct= new NgbDate(+year, +month, +day);
    this.currentLang = this.translateService.currentLang;
    this.translateService.onLangChange.subscribe((res: TranslationChangeEvent) => {
      this.currentLang = res.lang;
    });

  }

  dayTemplateData(date: NgbDate) {
    return {
      gregorian: (this.calendar as NgbCalendarHebrew).toGregorian(date)
    };
  }

  selectToday() {
    this.struct = this.calendar.getToday();
  }

}
