import { Component, OnInit } from '@angular/core';
import { NbLayoutDirection, NbLayoutDirectionService, NbSidebarService } from '@nebular/theme';
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'sim-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  hiddenSidebar: boolean = true;

  constructor(private directionService: NbLayoutDirectionService, private translateService: TranslateService, private sidebarService: NbSidebarService) { }

  ngOnInit(): void {
    // this.sidebarService.onCollapse().subscribe(e => console.log(e))
  }

  changeDirection(): void {
    this.directionService.getDirection() === 'ltr'
      ? this.directionService.setDirection(NbLayoutDirection.RTL)
      : this.directionService.setDirection(NbLayoutDirection.LTR);
    this.translateService.currentLang === 'en' ? this.translateService.use('heb') : this.translateService.use('en');
  }


}
