import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of, throwError } from 'rxjs';
import { tap } from 'rxjs/operators';

import { LoginRequest } from '@models/auth/login-request';
import { AuthResponse } from '@models/auth/auth-response';
import { environment } from '@environments/environment';


@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private http: HttpClient) { }

 
  login(data: LoginRequest, remember: boolean): Observable<AuthResponse> {
    return this.http.post<AuthResponse>(environment.api_prefix + 'auth/login', data).pipe(
      tap(authResponse => this.setTokens(authResponse, remember)));
  }

  isAuthenticated(): boolean {
    return Boolean(localStorage.getItem('access_token')) || Boolean(sessionStorage.getItem('access_token'));
  }

  private setTokens(authResponse: AuthResponse, remember: boolean): void {
    if (remember) {      
      localStorage.setItem('access_token', authResponse.access_token);
      localStorage.setItem('refresh_token', authResponse.refreshToken);
    } else {
      sessionStorage.setItem('access_token', authResponse.access_token);
      sessionStorage.setItem('refresh_token', authResponse.refreshToken);
    }
  }

  getToken(): string {
    return localStorage.getItem('access_token') ?? sessionStorage.getItem('access_token');
  }
}
